.welcome {
  width: 100vw;
  height: 100vh;
  background: {
    size: cover;
    image: url("../images/landing.jpg");
  }

  &::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(black, 0.5);
  }
  .holder {
    z-index: 100;
    width: fit-content;
  }
}