* {
  box-sizing: border-box;
}

html {
  scroll-behavior: smooth;
}
html * {
  outline: none;
}
html body {
  font-family: "Poppins", sans-serif;
}
html a {
  color: inherit;
  text-decoration: none;
}
html ul {
  margin: 0;
  padding: 0;
  list-style: none;
}
html img {
  width: 100%;
  height: 100%;
}
html button {
  border: none;
  cursor: pointer;
  background-color: transparent;
}

body > .welcome {
  width: 100vw;
  height: 100vh;
  background-size: cover;
  background-image: url("../images/landing.jpg");
}
body > .welcome::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
}
body > .welcome .holder {
  z-index: 100;
  width: fit-content;
}

body {
  height: 10000px;
}