.welcome {
  width: 100vw;
  height: 100vh;
  background-size: cover;
  background-image: url("../images/landing.jpg");
}
.welcome::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
}
.welcome .holder {
  z-index: 100;
  width: fit-content;
}